.carousel {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 1200px;
    height: 720px;
    padding: 20px;
    cursor: pointer;
    margin-top: 10px;
    margin-left: 10px; 
}

.slide {
    border-radius: 0.5rem;
    box-shadow: 0px 0px 7px #666;
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.slide-hidden {
    display: none;
}

.arrow {
    position: absolute;
    width: 2rem;
    height: 2rem;
    color: white;
    filter: drop-shadow(0px 0px 5px #555);
    margin: 0px 15px;
}

.arrow:hover {
    cursor: pointer;
}

.arrow-left {
    left: 1rem;
}

.arrow-right {
    right: 1rem;
}

.indicators {
    display: flex;
    justify-content: center;
    position: relative;
    top: -6.5rem; /* Adjust the position based on your design */
  }

.indicator {
    background-color: white;
    height: 0.5rem;
    width: 0.5rem;
    border-radius: 100%;
    border: none;
    outline: none;
    box-shadow: 0px 0px 5px #555;
    margin: 0 0.2rem;
    cursor: pointer;

}

.indicator-inactive {
    background-color: gray;
}

.imgDesc {
    margin-top: 10px;
    text-align: center;
  }